/*** Base ***/
.fuego {
  background: rgb(219, 77, 109);
  background: linear-gradient(90deg, rgb(219, 77, 109) 0%, rgb(255, 106, 0) 6%, rgb(255, 0, 0) 12%, rgb(233, 0, 0) 18%, rgb(255, 106, 0) 25%, rgb(224, 37, 47) 32%, rgb(255, 106, 0) 40%, rgb(255, 106, 0) 47%, rgb(255, 0, 0) 54%, rgb(255, 132, 0) 61%, rgb(255, 0, 0) 68%, rgb(255, 0, 0) 73%, rgb(255, 111, 0) 79%, rgb(255, 106, 0) 86%, rgb(255, 0, 0) 91%, rgb(255, 106, 0) 94%, rgb(255, 0, 0) 100%);
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", "sans-serif";
  font-size: 1.6rem;
  line-height: 1.8;
  max-width: 120rem;
  margin: 0 auto;
}

p {
  color: black;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  text-align: center;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, p {
  font-family: "Montserrat", "sans-serif";
  margin: 1rem/2 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.boton-amarillo-block {
  background-color: #b28442;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  width: 100%;
}
.boton-amarillo-block:hover {
  background-color: #9f763b;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #b28442;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  z-index: 3;
}
.boton-amarillo:hover {
  background-color: #9f763b;
  cursor: pointer;
}

a.boton-amarillo {
  background-color: #b28442;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  z-index: 3;
  appearance: none;
  color: #1a1915;
  text-transform: uppercase;
}
a.boton-amarillo:hover {
  background-color: #9f763b;
  cursor: pointer;
}

.boton-blanco {
  background-color: transparent;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border: 5px solid #ffffff;
}
.boton-blanco:hover {
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

.boton-blanco-negro {
  background-color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border: 3px solid #1a1915;
}
.boton-blanco-negro:hover {
  background-color: #f2f2f2;
  cursor: pointer;
}

.boton-negro {
  background-color: #1a1915;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border: 3px solid #ffffff;
  text-transform: uppercase;
  width: 30rem;
  margin: 0 auto;
  text-align: center;
  appearance: none;
  color: #ffffff;
}
.boton-negro:hover {
  background-color: #0c0b0a;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #0da6f3;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  max-width: 50%;
  min-width: 30%;
  margin: 0 auto;
  text-transform: uppercase;
  padding: 2rem;
  font-size: 1.5rem;
}
.boton-azul-block:hover {
  background-color: #0b96dc;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  color: #ffffff;
  width: 15rem;
  height: 5rem;
}
.boton-eliminar:hover {
  background-color: #b20000;
  cursor: pointer;
}

.boton-reserva {
  background-color: #329f00;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  color: #ffffff;
  width: 15rem;
  height: 5rem;
}
.boton-reserva:hover {
  background-color: #2a8600;
  cursor: pointer;
}

.boton-pagar {
  background-color: #329f00;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2rem;
}
.boton-pagar:hover {
  background-color: #2a8600;
  cursor: pointer;
}

.boton-cocina {
  background-color: #b28442;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2rem;
}
.boton-cocina:hover {
  background-color: #9f763b;
  cursor: pointer;
}

/*** Internas ***/
/*** Layout ***/
.formulario {
  padding: 2rem;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 2rem;
}
.formulario h2 {
  margin: 0;
  color: black;
}
.formulario label, .formulario input {
  width: 100%;
  max-width: 120rem;
  padding: 2rem;
  margin: 0 auto;
  display: block;
}
@media (min-width: 768px) {
  .formulario label, .formulario input {
    width: 70%;
  }
}
@media (min-width: 1200px) {
  .formulario label, .formulario input {
    width: 50%;
  }
}
.formulario input {
  border: 2px solid #030822;
}
.formulario input[placeholder] {
  text-align: center;
}
.formulario label {
  text-align: center;
}
.formulario button {
  margin-top: 3rem;
}
.formulario a {
  appearance: none;
  padding: 1rem;
  display: block;
}

.enlace {
  text-decoration: none;
  color: #1a1915;
  border: 2px solid #b28442;
  padding: 0.5rem;
  margin: 0 auto;
  margin-bottom: 4rem;
  display: block;
  width: 60%;
  font-size: 2rem;
}

.alerta {
  width: 90%;
  padding: 0.5rem;
  background-color: rgb(173, 48, 48);
  text-transform: uppercase;
  margin: 0 auto;
}

.error {
  color: #ffffff;
}

.header {
  width: 100%;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.85);
  min-height: 12rem;
}

.navegacion {
  background-color: transparent;
  display: block;
  position: relative;
  text-align: center;
}
.navegacion a {
  display: block;
  appearance: none;
  padding: 1rem;
  color: #1a1915;
  cursor: pointer;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin: 0 auto;
}
.navegacion a.logo {
  max-width: 12rem;
  min-width: 8rem;
}
.navegacion #menu {
  position: absolute;
  left: 2rem;
  top: 3rem;
}
.navegacion #menu .barras {
  max-width: 4rem;
  min-width: 4rem;
  display: block;
  filter: invert(70%);
}
.navegacion #menu .barras:hover {
  filter: invert(20%);
}
.navegacion .user {
  position: absolute;
  right: 2rem;
  top: 3rem;
  max-width: 4rem;
  min-width: 4rem;
  display: block;
}
.navegacion .user:hover {
  filter: invert(20%);
}

.activo {
  display: none;
}

#mobile-menu {
  appearance: none;
  list-style-type: none;
  width: 100%;
  text-align: center;
  padding: 1rem;
  padding-top: 0;
  margin: 0;
}
#mobile-menu a {
  text-decoration: none;
  display: block;
  appearance: none;
  padding: 1rem;
  color: #1a1915;
  cursor: pointer;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 700;
}
#mobile-menu a.logo {
  max-width: 15rem;
  min-width: 8rem;
}
#mobile-menu a:hover {
  color: #b28442;
}

.footer {
  font-weight: 700;
  background-color: #1a1915;
  color: #ffffff;
  margin-top: 2rem;
  padding: 3rem 1rem 2rem 1rem;
  text-align: center;
  margin: 0 auto;
}
.footer p {
  margin-top: 2rem;
  color: #ffffff;
}

.contacto {
  text-transform: uppercase;
  font-size: 1.5rem;
}
.contacto .contacto__numero {
  font-size: 2.5rem;
}

.footer__social {
  margin-top: 2rem;
  display: flex;
  text-align: center;
  justify-content: center;
  gap: 3rem;
  width: 100%;
}
.footer__social a img {
  filter: invert(100%);
  max-width: 5rem;
}

.footer__developer .copyright {
  color: #bc8d4b;
  font-size: 1rem;
}

