  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  font-weight: 700;
}
.contenedor__principal a {
  color: #ffffff;
  text-transform: uppercase;
  z-index: 3;
}
.contenedor__principal h1 {
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}
.contenedor__principal p {
  color: #ffffff;
}

.descripcion {
  width: 90%;
  margin: 0 auto;
}
.descripcion h2 {
  text-transform: uppercase;
  font-weight: 500;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#mesas {
  width: 60%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  #mesas {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}
#mesas .mesa {
  background-color: #329f00;
  cursor: pointer;
}
#mesas .mesa.reservada {
  cursor: none;
  opacity: 0.2;
  background-color: #cb0000;
}
#mesas .seleccionadoMesa {
  background-color: #0da6f3;
}
#mesas .seleccionadoMesa:hover {
  background-color: #6ecaf8;
}

.formulario {
  margin-bottom: 4rem;
}
.formulario input[type=date] {
  height: 35px;
  margin: 0 auto;
  min-width: 30%;
