  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
  border: 0;
  border-radius: 3px;
  background-color: rgb(158, 154, 154);
  padding: 0 3px;
  color: #1a1915;
  text-align: center;
}

.fechas {
  display: grid;
  width: 90%;
  grid-template-columns: 1fr 1fr;
  margin: 0 auto;
  justify-content: center;
  margin-bottom: 4rem;
  gap: 1rem;
}
@media (min-width: 768px) {
  .fechas {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.fechas .seleccionadoFecha {
  background-color: #0da6f3;
}
.fechas .seleccionadoFecha:hover {
  background-color: #6ecaf8;
}

.comensales {
  display: grid;
  width: 90%;
  grid-template-columns: 1fr 1fr;
  margin: 0 auto;
  justify-content: center;
  margin-bottom: 4rem;
  gap: 1rem;
}
@media (min-width: 768px) {
  .comensales {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.comensales .seleccionadoComensal {
  background-color: #0da6f3;
}
.comensales .seleccionadoComensal:hover {
