  padding: 1rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .comentarios textarea {
    min-width: 50rem;
  }
}
.comentarios textarea::placeholder {
  color: #1a1915;
  font-size: 2rem;
}

#sendAPI {
  margin-bottom: 4rem;
}

.nombre-usuario {
  color: #1a1915;
  background-color: #ffffff;
  font-size: 1.5rem;
  margin-bottom: 0;
}
.nombre-usuario span {
